.timeline {
  position: relative;
  min-height: 15rem;
}

.line {
  position: absolute;
  z-index: 2;
  left: calc( 50% - 1px);
  width: 2px;
  top: -5rem;
  bottom: -5rem;
  background-color: #69849b;
}

.line:before, .line:after {
  position: absolute;
  display: block;
  content: "";
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  background-color: #69849b;
  left: 50%;
  transform: translateX(-50%);
}

.line:after {
  bottom: 0;
}

.section {
  display: flex;
  position: relative;
  z-index: 1;
  padding: 1rem;
  align-items: center;
  min-height: 15rem;
}

.section:nth-child(even) {
  flex-direction: row-reverse;
}
@media (min-width: 768px) {
  .section:nth-child(even) .content {
    padding-left: 10rem;
  }
}

.bead {
  position: absolute;
  display: block;
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  background-color: #69849b;
  left: 50%;
  top: 50%;
  transform: translateX(-50%);
}

.content {
  width: calc(50% - 2rem);
  word-break: break-word;
}

